
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#break-point {
    text-align: center;
    width: 100vw;
    opacity: .8;
}
footer * {
    text-decoration: none;
}

@font-face {
    font-family: 'Coolesta';
    src: url('../fonts/Coolesta.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

:root {
    --cor0: #E6D5C3;
    --cor1: #efcab7;
    /* #E8CBB9*/
    --cor2: #D5A988;
    --cor3: #A06E52;
    /* #e15e41 */
    --cor4: #583828;

    --fonte-padrao: 'Raleway', sans-serif;
    --fonte-coolesta: 'Coolesta', cursiva
}



.text-colored {
    color: var(--cor3);
}

.bg-colored--lightest {
    background-color: var(--cor1);
}
.bg-colored--light {
    background-color: var(--cor2);
}
.bg-colored {
    background-color: var(--cor3);
}
.bg-colored--dark {
    background-color: var(--cor4);
}

body::-webkit-scrollbar {
    display: none;
}

* {
    font-family: var(--fonte-padrao);
}

ul li {
    list-style: none;
}

ul li a,
footer a {
    text-decoration: none;
}

.container-md p {
    text-indent: 0.5in;
    text-align: justify;
}

header {
    background-color: var(--cor1);
    height: max-content;
}

#header_menu ul.nav .nav-link.active,
#header_menu ul.nav .nav-link:hover {
    background-color: var(--cor3);
    color: var(--cor1);
}

#header_menu a:visited {
    color: var(--cor3);
}

.scroll-spy {
    height: 75vh;
    overflow: auto;
}

.carousel-inner img {
    max-height: 70vh;
}

#carousel.carousel button.carousel-control-prev:hover {
    background-image: linear-gradient(to left, transparent 20%, rgba(0, 0, 0, 0.4) 100%);
}

#carousel.carousel button.carousel-control-next:hover {
    background-image: linear-gradient(to right, transparent 20%, rgba(0, 0, 0, 0.4) 100%);
}

#carousel.carousel button span {
    width: 60px;
    height: 90px;
}

.carousel-indicators .active {
    background-color: var(--cor3);
}

body .container-md>section {
    /* min-height: 70vh; */
    padding: 2em 1em;
    margin-bottom: 12px;
}

body .container-md>section:nth-child(odd) {
    background-color: var(--cor0);
}

section h2 {
    border-bottom: 1px solid var(--cor3);
    font-family: var(--fonte-coolesta);
    font-weight: bold;
}

section .form-floating textarea:focus,
section .form-floating input:focus {
    border-color: var(--cor1);
    box-shadow: 0px 0px 10px var(--cor3);
}

textarea {
    resize: none;
    height: 10rem !important;
}

footer * {
    /* background-color: transparent; */
}

.accordion-button{
    color: var(--cor4);
    background-color: var(--cor1);
}
.accordion-button:not(.collapsed){
    color: var(--cor4);
    background-color: var(--cor2);
}

.accordion-button:focus {
    border-color: var(--cor2);
    box-shadow: 0 0 0 0.25rem rgba(213, 169, 136, 0.25);
    
}

.accordion li {
    list-style: square;
}

#whats_btn {
    height: 100px;
    width: 100px;
    background-color: rgba(0, 128, 0, 0.33);

    position: fixed;
    right: 2vw;
    bottom: 2vh;

    transition: all .3s;

    z-index: 100;
    cursor: pointer;

    i {
        position: relative;
        left: 15%;
        font-size: 4.5em;
        color: rgba(255, 255, 255, 0.33);
    }

    &:hover {
        transform: scale(1.1);
        background-color: rgba(0, 128, 0, 1);

        i {
            color: white;
        }
    }

    @media (max-width: 640px) {
        height: 75px;
        width: 75px;

        i {
            font-size: 3.3em;
        }
    }
}
